}\n  }\n}\n\n@mixin border-std {\n  border: 1px solid var(--color-border);\n  border-radius: var(--card-radius);\n}\n","@import '~ui/scss/init/breakpoints';\n@import '~ui/scss/init/mixins';\n\n$list-height: 40vh;\n$list-height--mobile: 30vh;\n\n.channel-finder {\n  padding: var(--spacing-m) 0;\n\n  @media (max-width: $breakpoint-small) {\n    padding: var(--spacing-xs) 0;\n  }\n}\n\n.channel-finder__middle {\n  display: flex;\n  padding: var(--spacing-s) var(--spacing-xs);\n\n  @media (max-width: $breakpoint-small) {\n    flex-direction: column;\n  }\n}\n\n.channel-finder__bottom {\n  padding-left: var(--spacing-xs);\n}\n\n.channel-finder__suggestion-list {\n  width: 50%;\n  height: $list-height;\n  padding: var(--spacing-xs) var(--spacing-s);\n  overflow: auto;\n  border-radius: var(--border-radius);\n  background: var(--color-header-button);\n\n  @media (max-width: $breakpoint-small) {\n    width: 100%;\n    height: $list-height--mobile;\n  }\n}\n\n.channel-finder__selected-list {\n  width: 50%;\n  flex-grow: 1;\n  margin-left: var(--spacing-s);\n  padding: var(--spacing-xs) var(--spacing-s);\n  height: $list-height;\n  overflow: auto;\n  border-radius: var(--border-radius);\n  background: var(--color-header-button);\n\n  @media (max-width: $breakpoint-small) {\n    margin-left: 0;\n    margin-top: var(--spacing-s);\n    width: 100%;\n    height: $list-height--mobile;\n  }\n}\n\n.channel-finder__selected-list-label {\n  align-self: flex-end;\n  border-bottom: 1px solid var(--color-border);\n  padding-bottom: var(--spacing-xs);\n  margin-bottom: var(--spacing-xs);\n  color: var(--color-text-subtitle);\n}\n\n.channel-finder__misc-suggestions {\n  label {\n    font-size: var(--font-small);\n    color: var(--color-text-subtitle);\n    padding-top: var(--spacing-m);\n    padding-bottom: var(--spacing-xxs);\n  }\n}\n\n.channel-finder__misc-suggestions:not(:first-child) {\n  border-top: 1px solid var(--color-border);\n  margin-top: var(--spacing-m);\n}\n\n// ************